body{
  font-family: 'Dosis', sans-serif;
  background-color: #d5c9f1;
animation-name: bg;
animation-duration: 7s;
animation-iteration-count: infinite;
}
@keyframes bg{
  0% {background-color: #d5c9f1;}
  20% {background-color: #d1abf9;}
  40% {background-color: #c696ff;}
  60% {background-color: #c296f4;}
  80% {background-color: #bb9ffc;}
  100% {background-color: #d5c9f1;}
}
.btn-color{
  background-color: #9f7fec;
}
.img-tran{
  transform: none;
  transition: transform 2s;
  transition-timing-function: ease-out
}
.img-tran:hover{
transform: rotateX(160deg);
}

.my-icon{
  color: #9f7fec;
  text-align: none;
  animation-name: example;
  animation-duration: 3s;
  text-align: center;
  animation-iteration-count: infinite;
}
@keyframes example {
from{color: #9f7fec;}
to{color: black;}
}
.img-tran2{
  transform: none;
  transition: transform 2s;
  transition-timing-function: ease-out
}
.img-tran2:hover{
  transform: rotateY(160deg);
  }
